<template>
  <div class="detail">
    <div class="top"></div>
    <van-nav-bar
      title="商品详情"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="detail-box">
      <img
        class="auto-img"
        :src="product.large_img"
        alt=""
      >
      <div class="pro-name">
        <div>{{product.name}}</div>
      </div>
    </div>
    <div class="detail-info">
      <div class="rule-box">
        <div
          class="rule-item clearfix"
          v-for="(item,index) in product.rules"
          :key="index"
        >
          <div class="rule-item-title fl">{{item.title}}</div>
          <div class="fl clearfix">
            <div
              class="rule-item-tag fl"
              :class="{active: v.isActive}"
              v-for="(v,i) in item.rules"
              :key="i"
              @click="toggleRule(v,index)"
            >{{v.title}}</div>
          </div>
        </div>
      </div>
      <div class="desc-box">
        <div class="desc-title">商品描述</div>
        <div class="desc-content">
          <div
            class="desc-text"
            v-for="(item,index) in product.desc"
            :key="index"
          >{{index + 1}}、{{item}}</div>
        </div>
      </div>
      <div class="desc-price-count clearfix">
        <div class="price fl">￥{{product.price}}</div>
        <div class="count fr">
          <van-stepper
            v-model="count"
            theme="round"
            button-size="22"
            disable-input
            color="#fff"
          />
        </div>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon
        icon="bag-o"
        text="购物袋"
        :badge="bagcount == 0 ? '' : bagcount"
        color="#999"
        @click="goShopbag"
      />
      <van-goods-action-icon
        :icon="isLike ? 'like' : 'like-o'"
        text="收藏"
        :color="isLike ? '#FF0036': '#999'"
        @click="likeAndNotlike"
      />
      <van-goods-action-button
        color="#6A91EC"
        text="加入购物袋"
        @click="addShopbag"
      />
      <van-goods-action-button
        color="#0C34BA"
        text="立即购买"
        @click="buyNow"
      />
    </van-goods-action>
  </div>
</template>

<script>
import "../assets/less/detail.less";
import { utils } from "../assets/js/Utils";
export default {
  data() {
    return {
      count: 0,
      pid: "",
      product: {},
      isLike: false,
      bagcount: 0,
    };
  },
  created() {
    this.pid = this.$route.query.pid;
    //

    // 根据商品pid获取商品数据
    this.getProductByPid();

    // 查询商品是否被收藏
    this.findlike();

    // 查询购物袋的数目
    this.findShopbag();
  },
  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },

    // 回到购物袋
    goShopbag() {
      this.$router.push({ name: "Shopbag" });
    },

    // 根据商品pid获取商品数据
    getProductByPid() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/productDetail",
        //如果get请求，参数需要放在params, 如果是post请求,参数需要放在data
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 600) {
            // 处理商品描述数据
            result.data.result[0].desc = result.data.result[0].desc.split(/\n/);

            // 处理商品规格数据
            /**
             * [
                {
                  title: '温度',
                  rules: [
                    {
                      title: '热',
                      isActive: false
                    }
                  ]
                }
              ]
            */
            let ruleItems = ["tem", "sugar", "cream", "milk"];
            let rulesData = [];
            ruleItems.map((v) => {
              if (result.data.result[0][v] == "") {
                return;
              }

              let rule = {
                title: result.data.result[0][v + "_desc"],
                rules: [],
              };
              let r = result.data.result[0][v].split("/");
              r.map((value, index) => {
                let o = {
                  title: value,
                  isActive: index == 0,
                };
                rule.rules.push(o);
              });
              rulesData.push(rule);
            });
            result.data.result[0].rules = rulesData;

            this.product = result.data.result[0];

            //
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 切换规格
    toggleRule(v, index) {
      if (v.isActive) {
        return;
      }
      let ruleData = this.product.rules[index].rules;
      for (let i = 0; i < ruleData.length; i++) {
        if (ruleData[i].isActive) {
          ruleData[i].isActive = false;
          break;
        }
      }
      v.isActive = true;
    },

    // 查询商品是否被收藏
    findlike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isLike = true;
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 是否收藏
    likeAndNotlike() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
      });
      let url = this.isLike ? "/notlike" : "/like";

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            return this.$router.push({ name: "Login" });
          }
          this.$toast({
            message: result.data.msg,
            color: "#444",
            icon: "like",
            closeOnClick: true,
            duration: 1000,
          });

          if (result.data.code == 800) {
            this.isLike = true;
          } else if (result.data.code == 900) {
            this.isLike = false;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 查询购物袋
    findShopbag() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            this.bagcount = result.data.result.length;
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 加入购物袋
    addShopbag(isBuy) {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      // 获取选择的规格
      let rules = this.product.rules;

      let rule = [];
      rules.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            rule.push(v.rules[i].title);
            break;
          }
        }
      });
      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: rule.join("/"),
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          this.$toast(result.data.msg);

          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              // 累计购物车徽章数量,购物车总条数，而不是购物车商品总数量
              this.bagcount++;
            }

            if (isBuy === true) {
              // 立即购买
              setTimeout(() => {
                this.$router.push({
                  name: "Pay",
                  query: { sids: result.data.sid },
                });
              }, 600);
            }
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 立即购买
    buyNow() {
      this.addShopbag(true);
    },
  },
};
</script>